(function (window, game) {
    var self = this;
    var state_name = "07";
    var images_path = "./states/" + state_name + "/images/";
    var audio_path = "./states/" + state_name + "/images/";
    var bFlga = true;
    var dianji = false;
    var xxk = "0xf1df59";
    var temr = 10;
    var bFlag = true;

    game.state.add(state_name, function () {
        // this.feeling_texts = [];
        this.feeling_tween = [];
        this.mood = []; //心情 数组
        var percent = [];

        this.init = function (data) {
            game.scale.pageAlignHorizontally = true;
            game.scale.pageAlignVertically = true;
            game.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL;
            game.physics.startSystem(Phaser.Physics.ARCADE)
            //game.stage.setBackgroundColor(0xCCCCCC);
            this.data = data;
            dianji = false;
            if (data === undefined) {
                this.data = {
                    title: 'heheheh',
                    num: [999, 66, 45]
                };
            }
            this.groupA = null; //存放题支组
            this.score = 0;
            self = this;
            this.feeling_texts = [];
            this.game_but = null;
        };
        this.preload = function () {
            //学生显示加载
        };
        //学生获取投票结果
        this.getItemVote = function (item) {

            for (name in item) {

                for (var i = 0; i < this.data.body.length; i++) {
                    if (this.data.body[i].name == name) {
                        if (item[name] == "") {
                            item[name] = 0;
                        }
                        this.feeling_texts[i].text = item[name];
                        console.log("显示人数=====")
                        // log("显示人数====1===")
                        // log(item[name])
                        this.feeling_texts[i].alpha = 1;
                        // if(gameManager.is_teacher){
                        //     this.feeling_texts[i].text = item[name];
                        //     //this.feeling_texts[i]=game.add.text(this.data.body[i].pos.x,this.data.body[i].pos.y,item[name]+'人',{fill:"#ab8c60",fontSize:38,align:"center",boundsAlignH: "center"})
                        // }else {
                        //     this.feeling_texts[i].text = item[name];
                        //     this.feeling_texts[i].alpha = 1;
                        //     //this.feeling_texts[i]=game.add.text(this.data.body[i].pos.x,this.data.body[i].pos.y,item[name]+'人',{fill:"#ab8c60",fontSize:38,align:"center",boundsAlignH: "center"})
                        // }

                    }
                }
            }

        };
        this.create = function () {
            var other_images = this.data.other_images;

            if (other_images) {
                for (var tem_i = 0; tem_i < other_images.length; tem_i++) {

                    other_images[tem_i]['image'] !== '' ? game.add.image(other_images[tem_i]['pos']['x'], other_images[tem_i]['pos']['y'], game.stateIndex + 'other_images' + tem_i + 'temp') : null;
                }
            }

            var self = this;
            if (this.data.overviewImage != "") {
                game.add.image(this.data.pos.x, this.data.pos.y, game.stateIndex + 'overviewImage');
            }
            if (this.data.titleImage != "" && this.data.titleImage != undefined) {
                game.add.image(this.data.text_pos.x, this.data.text_pos.y, game.stateIndex + 'titleImage');
            }
            var title = game.add.text(this.data.text_pos.x, this.data.text_pos.y, this.data.title, { fill: this.data.fill_color, fontSize: this.data.font_size, align: "center", boundsAlignH: "center" });
            title.setTextBounds(10, 0, 700, 40);
            title.wordWrapWidth = 900;
            title.wordWrap = true;
            this.groupA = game.add.group();

            for (var i = 0; i < this.data.body.length; i++) {
                this.groupA.add(game.add.button(this.data.body[i].pos.x, this.data.body[i].pos.y, game.stateIndex + 'body_image' + i, this.onClick, this));
                var current = this.groupA.children[i];
                current.id = i;
                current.name = this.data.body[i].name;
                current.alpha = 0.5;
            }

            for (var i = 0; i < this.data.body.length; i++) {
                var x = (this.data.body[i].pos.x + (this.groupA.children[i].width / 2))
                this.feeling_texts[i] = game.add.text(this.data.body[i].pos.x, this.data.body[i].pos.y, this.data.body[i].nameNum, { fill: "#407eff", fontSize: 28, align: "center", boundsAlignH: "center" })
                this.feeling_texts[i].setTextBounds(0, 0, this.groupA.children[i].width, 40);//已题支宽度居中显示
                if (gameManager.is_teacher) {
                    this.feeling_texts[i].alpha = 1;
                } else {
                    this.feeling_texts[i].alpha = 0;
                }
            }
            /*老师开始答题*/
            if (game.is_teacher) {
                //老师显示界面
                var c = 0x6dc7eb;
                var q_text_length = 4;
                var bg = game.add.image(60, 550, 'go');
                this.game_but = game.add.text(5, 10, "GO", { fill: "#000000", fontSize: 60, fontWeight: "normal", boundsAlignH: "center", boundsAlignV: "middle" });
                this.game_but.setTextBounds(35, 38, 16 * q_text_length / 2, 30);
                bg.addChild(this.game_but);
                this.game_but.inputEnabled = true;
                var self = this;
                this.game_but.events.onInputUp.add(function () {

                    if (!bFlga) {
                        return false; //防止重复点击
                    }
                    if (bFlga) {
                        bFlga = false;
                        var t = 9;
                        var daojis = setInterval(function () {
                            t--;
                            if (t === 0) {
                                self.game_but.text = "END";
                                self.game_but.fontSize = 40;
                                c = 0xF5F5F5;
                                self.game_but.style.color = 0xCD0000;
                                clearInterval(daojis);
                                bFlga = true;
                                return false;
                            }
                            self.game_but.text = t + "s";
                        }, 1000);
                    }


                    gameManager.startTesting();
                });
            } else {
                //学生端
                game.show_mengban();
                //添加 倒计时进度 条
                game.progressBar = this.add_progress_bar(0, 0, 758);
                game.progressBar.alpha = 0;

                // //测试
                // this.startgame()
            }
            gameCommon();

        }
        //点击选择投票
        this.onClick = function (button) {
            if (game.is_teacher) {
                //老师不可点击
                return;
            }
            if (dianji) {
                return;
            }
            game.add.tween(button).to({ width: button.width + 20, height: button.height + 20 }, 100, null, true, 0, 1, true);

            gameManager.postVote(button.name); //学生发送投票结果
            this.score = 100;
            this.submitScore(100, -1); //学生提交分数给直播云
            dianji = true;
        }
        var timerBarHeight = 10; //timer bar height
        var timerBarColour = 0x43CD80;; //timer bar colour 进度条颜色
        /*!
         *  绘制定时器进度条 Progress bar
         */
        this.add_progress_bar = function (anime, x, y) {
            var progress = game.add.graphics(0, 0);
            progress.beginFill(timerBarColour); //颜色16进制，透明度0-1之间
            progress.drawRect(0, 0, game.width, timerBarHeight); //矩形
            progress.endFill();
            var sprite = game.add.sprite(x, y, progress.generateTexture());
            progress.destroy();
            return sprite;
        }
        //提交分数
        this.submitScore = function (scores, animation) {
            if (self.wrong) {
                animation = 4;
            }

            // gameManager.sendScore(100,100,animation);
            gameManager.stars(Math.round(scores)); //demo使用
            ScoreAnimtions(scores)

            // gameManager.addRecord(scores)//上传服务器课程数据
            console.log('星星数量==互动题===', gameManager.start)
            clearInterval(game.temp_t);

        }
        /**==翻页笔调用页面开始答题==**/
        this.answers_startgame = function () {
            var c = 0x6dc7eb;
            if (game.is_teacher) {
                if (this.game_but != null) {
                    var self = this;
                    if (!bFlga) {
                        return false; //防止重复点击
                    }
                    if (self.game_but.text != "END") {
                        if (bFlga) {
                            bFlga = false;
                            var t = 9;
                            var daojis = setInterval(function () {
                                t--;
                                if (t === 0) {
                                    self.game_but.text = "END";
                                    self.game_but.fontSize = 40;
                                    c = 0xF5F5F5;
                                    self.game_but.style.color = 0xCD0000;
                                    clearInterval(daojis);
                                    bFlga = true;
                                    return false;
                                }
                                self.game_but.text = t + "s";
                            }, 1000);
                        }
                    }
                    if (self.game_but.text != "END") {
                        gameManager.startTesting();
                    }

                }
            }
        }
        //关闭蒙版
        this.startgame = function (time_out) {
            if (time_out == undefined) {
                time_out = 7;
            }
            var self = this;
            if (!gameManager.is_teacher) {
                for (var i = 0; i < this.groupA.length; i++) {
                    var current = this.groupA.children[i];
                    current.alpha = 1;
                }
                game.progressBar.alpha = 1;
                game.show_mengban(true);
                var timerStart = 8000; //timer start number  10秒
                game.add.tween(game.progressBar).to({ width: 0, alpha: 0 }, timerStart, Phaser.Easing.Linear.None, true).onComplete.add(function () {
                    var data = json_data.courseware[gameManager.tid]
                    // gameManager.log('学生获取投票结果==互动数据==',data)
                    if (data) {
                        var bodys = data.data.body;
                        var itemArr = new Array();
                        for (var i = 0; i < bodys.length; i++) {
                            // self.getVote(bodys[i].name)
                            itemArr.push(bodys[i].name)
                        }
                        dianji = true;
                        // gameManager.log('学生获取投票结果==服务器获取==',data)
                        gameManager.getVote(itemArr)
                        if (self.score == 0) {
                            gameManager.sendScore(self.score, 100, -1);
                        }
                    }
                }); //在到及时 减掉本身宽度; //在到及时 减掉本身宽度
                //var time_show = game.add.text(10,10,time_out);
                game.temp_t = setInterval(function () {
                    time_out--;
                    temr--;
                    if (time_out === 0) {
                        bFlag = false;
                        dianji = true;
                        clearInterval(game.temp_t);
                        //答题结束 学生 获取 投票结果
                        //  gameManager.log('学生获取投票结果==互动page==',gameManager.tid)
                        self.submitScore(self.score, 3); //传给直播云 互动 时间到没有答题

                    }
                }, 1000);
                if (this.data.voice != undefined && this.data.voice != "") {
                    // game.sound.context.resume();
                    game.sound.play("voice");
                }
            }

        }
        this.stopgame = function (data) {
            // self.log("收到投票结果========");
            // self.log(data)
            // console.log("收到投票结果========",data);
            if (game.is_teacher) {
                return;
            }

        }
    });

})(window, game);