(function (window, game) {
    var state_name = "10";
    var images_path = "./states/" + state_name + "/images/";
    var audio_path = "./states/" + state_name + "/images/";
    var BackgroundColor = 0xffffff; //游戏画布背景
    game.state.add(state_name, function () {
        var self = this;
        var bFlga = true;
        var temr = 10;
        this.init = function (data) {
            game.scale.pageAlignHorizontally = true; //水平居中
            game.scale.pageAlignVertically = true; //垂直居中
            game.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL;
            game.stage.setBackgroundColor(BackgroundColor);
            bFlga = true;
            this.dianji = false;
            this.data = data;
            this.fensu = 0;
            this.wrong = false;  //是否答错
            this.buttonId = null;
            this.body_group = null
            this.game_but = null;
            this.groupA = null; //选项框
            this.groupB = null; //选项
            this.isAnswer = false; //是否开始答题
            this.TwisRunning = false; //是否动画中
            this.random = false; //不开启
            this.answerNum = 0; //答案数量 根据答对 
            for (var a = 0; a < this.data.groupA.length; a++) {
                this.answerNum = this.data.groupA[a].answer.length + this.answerNum;
            }
            this.scoreNum = 0; //用户答对数量
            // console.log('答案数量',this.answerNum)
        };

        this.preload = function () {

        };
        /***添加标题***/
        this.addTitle = function () {
            if (this.data.title_img != "" && this.data.title_img != undefined) {
                game.add.image(this.data.text_pos.x, this.data.text_pos.y, game.stateIndex + 'title_img');
            }
        };

        this.roleInit = function () {
            if (game.is_teacher) {
                //老师显示界面
                var c = 0x6dc7eb;
                var q_text_length = 4;
                var bg = game.add.image(60, 550, 'go');
                this.game_but = game.add.text(5, 10, "GO", { fill: "#000000", fontSize: 60, fontWeight: "normal", boundsAlignH: "center", boundsAlignV: "middle" });
                this.game_but.setTextBounds(35, 38, 16 * q_text_length / 2, 30);
                bg.addChild(this.game_but);
                this.game_but.inputEnabled = true;
                var self = this;
                this.game_but.events.onInputUp.add(function () {
                    if (!bFlga) {
                        return false; //防止重复点击
                    }
                    if (bFlga) {
                        bFlga = false;
                        var t = 9;
                        var daojis = setInterval(function () {
                            t--;
                            if (t === 0) {
                                self.game_but.text = "END";
                                self.game_but.fontSize = 40;
                                c = 0xF5F5F5;
                                self.game_but.style.color = 0xCD0000;
                                clearInterval(daojis);
                                bFlga = true;
                                return false;
                            }
                            self.game_but.text = t + "s";
                        }, 1000);
                    }
                    gameManager.startTesting();
                });
            } else {
                game.show_mengban();
                //添加 倒计时进度 条
                game.progressBar = this.add_progress_bar(0, 0, 758);
                game.progressBar.alpha = 0;
            }


        };
        //添加选项框
        this.addGroupA = function () {
            this.groupA = game.add.group();
            this.groupA.enableBody = true;
            this.groupA.physicsBodyType = Phaser.Physics.ARCADE;
            for (var i = 0; i < this.data.groupA.length; i++) {
                var currenta = this.data.groupA[i];
                if (currenta.pos != undefined || currenta.pos != "") {
                    this.groupA.create(currenta.pos.x, currenta.pos.y, game.stateIndex + 'groupA_image' + i);
                }
            }
            for (var n in this.groupA.children) {
                this.groupA.children[n].id = n; //
                this.groupA.children[n].answers = this.data.groupA[n].answer;
            }
        };
        //填加选项
        this.addGroupB = function () {
            this.groupB = game.add.group();
            this.groupB.enableBody = true;
            this.groupB.physicsBodyType = Phaser.Physics.ARCADE;
            for (var i = 0; i < this.data.groupB.length; i++) {
                var currenta = this.data.groupB[i];
                if (currenta.pos != undefined || currenta.pos != "") {
                    this.groupB.create(currenta.pos.x, currenta.pos.y, game.stateIndex + 'groupB_image' + i);
                }
            }
            for (var n in this.groupB.children) {
                this.groupB.children[n].id = n; //
                this.groupB.children[n].answer = false; //是否答对
                this.groupB.children[n].anchor.setTo(0.5, 0.5);

            }

            self.startAnswer();

        }
        //开始答题
        this.startAnswer = function () {
            // console.log("开始答题并拖拽");
            var randlist = random(this.data.groupB.length); //开启随机"
            if (this.random) {
                var randlist = random(this.data.groupB.length); //开启随机"
            }
            for (var n in this.groupB.children) {
                this.groupB.children[n].id = n; //
                var item = this.groupB.children[n];
                // console.log("开启随机",randlist[n]); //保证不是每次打开都是一样的
                if (this.random) {
                    var x = this.data.groupB[randlist[n] - 1].pos.x + item.width / 2;
                    var y = this.data.groupB[randlist[n] - 1].pos.y + item.height / 2;
                } else {
                    var x = this.data.groupB[n].pos.x + item.width / 2;
                    var y = this.data.groupB[n].pos.y + item.height / 2;
                }

                this.groupB.children[n].old = { "x": x, "y": y };
                item.inputEnabled = true;
                item.input.enableDrag();
                item.events.onDragStop.add(this.DragStop, this);
                item.events.onDragStart.add(this.DragStart, this);
                self.TwisRunning = true;
                game.add.tween(item).to({ x: x, y: y }, 400, null, true, 0, 0).onComplete.add(function () {
                    // console.log("开始答题动画完成",self.isAnswer);
                    self.TwisRunning = false; //动画完成可以拖拽

                });
            }
            //随机函数
            function random(m) {
                var arr = [];
                for (var i = 0; i < m; i++) {
                    var n = rnd(1, m + 1);
                    if (!findinArr(arr, n)) {
                        arr.push(n);
                    }
                    else {
                        i--;
                    }
                }
                function findinArr(arr, n) {
                    for (var i = 0; i < arr.length; i++) {
                        if (arr[i] == n) {
                            return true;
                        }
                    }
                    return false;
                }
                function rnd(n, m) {
                    return parseInt(Math.random() * (m - n) + n);
                }
                return arr;

            }
        };
        /*****===开始拖拽==***/
        this.DragStart = function (image) {
            // console.log("开始拖拽===",!image.answer);
        };
        /*****===拖拽结束==***/
        this.DragStop = function (image) {

            var ls = game.physics.arcade.overlap(image, this.groupA, this.check_over, null, this);
            // console.log("拖拽结束=2==",ls)
            // console.log("拖拽结束===",image.answer)

            if (!ls) {
                console.log("没发生碰撞的", ls)
                var x = image.old.x;
                var y = image.old.y;
                // e1.answer = false;

                game.add.tween(image).to({ x: x, y: y }, 300, null, true, 0, 0).onComplete.add(function () {
                    //    image.inputEnabled = true;
                    //    self.TwisRunning = false;
                    // game.sound.context.resume();
                    game.sound.play('errolplay')
                });
            }
        };
        //检测碰撞
        this.check_over = function (e1, e2) {

            for (var i = 0; i < e2.answers.length; i++) {

                if (e1.id == e2.answers[i]) {
                    //  e1.anchor.setTo(0.5,0.5);
                    var width = e1.width + 30;
                    var height = e1.height + 30;
                    var x = this.data.groupA[e2.id].pos.x;
                    var y = this.data.groupA[e2.id].pos.y

                    var w = this.groupA.children[e2.id].width / 2;
                    var h = this.groupA.children[e2.id].height / 2;
                    game.add.tween(e1).to({ x: this.data.groupA[e2.id].pos.x + w, y: this.data.groupA[e2.id].pos.y + h }, 300, null, true, 0, 0).onComplete.add(function () {
                        // e1.inputEnabled = false;
                        e1.anchor.setTo(0.5, 0.5);
                        game.sound.stopAll();
                        // game.sound.context.resume();
                        game.sound.play('goodplay')
                        var tw = game.add.tween(e1).to({ height: height, width: width, alpha: [1, 0.5, 0] }, 400, Phaser.Easing.Bounce.Outue, true, 0, 0);
                        tw.onComplete.add(function () {
                            self.scoreNum = self.scoreNum + 1;
                            if (self.scoreNum == self.answerNum) {
                                console.log("全部答对");
                                self.submitScore(100)
                            }
                            e1.inputEnabled = false;
                        })

                    });;
                    return true;
                } else {
                    //错返回
                    var x = e1.old.x;
                    var y = e1.old.y;

                    game.add.tween(e1).to({ x: x, y: y }, 300, null, true, 0, 0).onComplete.add(function () {
                        game.sound.stopAll();
                        // game.sound.context.resume();
                        game.sound.play('errolplay')
                    });
                }
            }


        };
        //判卷
        this.judgeAnswer = function () {

            return true;
        }

        this.addItme = function () {
            if (this.data.item != "" && this.data.item != undefined) {

                for (var i = 0; i < this.data.item.length; i++) {

                    if (this.data.item[i].image != "") {
                        // console.log("22222",this.data.item[i].image)
                        game.add.image(this.data.item[i].pos.x, this.data.item[i].pos.y, game.stateIndex + 'item_image' + i);
                    }
                }

            }
        }
        this.create = function () {
            var self = this;
            console.log("进入分类==", this.data.item);
            //添加标题
            this.addTitle();
            this.addItme();
            //组A
            this.addGroupA();
            //组B
            this.addGroupB();
            gameCommon();
            //初始化身份
            this.roleInit();
        }


        var timerBarHeight = 10; //timer bar height
        var timerBarColour = 0x43CD80; //timer bar colour 进度条颜色
        /*!
         *  绘制定时器进度条 Progress bar
         */
        this.add_progress_bar = function (anime, x, y) {
            var progress = game.add.graphics(0, 0);
            progress.beginFill(timerBarColour);//颜色16进制，透明度0-1之间
            progress.drawRect(0, 0, game.width, timerBarHeight);//矩形
            progress.endFill();
            var sprite = game.add.sprite(x, y, progress.generateTexture());
            progress.destroy();
            return sprite;
        }

        //提交分数
        this.submitScore = function (scores, animation) {
            if (self.wrong) {
                animation = 4;
            }

            gameManager.stars(Math.round(scores)); //demo使用
            if (scores == 0) {
                return;
            }
            if (game.progressBar) { game.progressBar.alpha = 1; }
            ScoreAnimtions(scores)
            console.log('星星数量==2===', gameManager.start)
            clearInterval(game.temp_t);

        }
        /**==翻页笔调用页面开始答题==**/
        this.answers_startgame = function () {
            var c = 0x6dc7eb;
            if (game.is_teacher) {
                if (this.game_but != null) {
                    var self = this;
                    if (!bFlga) {
                        return false; //防止重复点击
                    }
                    if (self.game_but.text != "END") {
                        if (bFlga) {
                            bFlga = false;
                            var t = 9;
                            var daojis = setInterval(function () {
                                t--;
                                if (t === 0) {
                                    self.game_but.text = "END";
                                    self.game_but.fontSize = 40;
                                    c = 0xF5F5F5;
                                    self.game_but.style.color = 0xCD0000;
                                    clearInterval(daojis);
                                    bFlga = true;
                                    return false;
                                }
                                self.game_but.text = t + "s";
                            }, 1000);
                        }
                    }
                    if (self.game_but.text != "END") {
                        gameManager.startTesting();
                    }

                }
            }
        }
        /***这是运行游戏定时器的功能 游戏中的定时器 定义***/
        this.startgame = function (time_out) {
            if (time_out == undefined) {
                time_out = 7;
            }
            game.progressBar.alpha = 1;
            game.show_mengban(true);
            var timerStart = 8000; //timer start number  10秒
            game.add.tween(game.progressBar).to({ width: 0, alpha: 0 }, timerStart, Phaser.Easing.Linear.None, true).onComplete.add(function () {
                //答题完成
                self.submitScore(0, 4) //时间到没打完就算错误
            });

        }
    });


})(window, game);