        //绘制每一笔播放  回放
        this.playback  = function(paintData){
            isPlay =  true; //没播放
            var self = this;
            var  Dindex = 0;
            var qinaaa = setInterval(function(){
                Dindex++; //每一个笔画的索引
                if(playIndex == self.PaintRoute.length){
                    //停止播放
                    playIndex = 0;
                    clearInterval(qinaaa); //清楚一笔播放
                    // isPlay =  false; //没播放
                    // self.PaintRoute = [];
                    return false; //防止重复点击
                }


                if(Dindex == paintData.length){
                    clearInterval(qinaaa); //清楚一笔播放
                    // playIndex ++;
                    if(self.PaintRoute.length>1){
                        if(playIndex == self.PaintRoute.length){
                            //停止播放
                            playIndex = 0;
                            clearInterval(qinaaa); //清楚一笔播放

                            return false; //防止重复点击
                        }else{

                            playIndex ++;
                            self.playback(self.PaintRoute[playIndex]);
                        }

                    }
                    // console.log("每一个笔画索引==结束==",Dindex)
                }else{
                    // console.log("每一个笔画索引====",Dindex)
                    for (var i = 0; i <= 1; i += 0.001) {
                        // Dindex++
                        // 线性算法生成路径点
                        // console.log("每一个笔画索引===2=",Dindex,[paintData.x[Dindex - 1]])
                        var dpx1 = [paintData[Dindex - 1][0],[paintData[Dindex][0]]];
                        // // var dpx2 = [paintData.x[Dindex]];
                         var dpy1 = [paintData[Dindex - 1][1],[paintData[Dindex][1]]];
                        // var dpy2 = [paintData.y[Dindex]];
                        var px = self.math.catmullRomInterpolation(dpx1, i);
                        var py = self.math.catmullRomInterpolation(dpy1, i);
                        if (!isNaN(px) && !isNaN(py) ) {
                            self.bmd.draw(self.bmd2, px, py);
                        }
                    }
                }

            },100)
        }

        //绘制请求答案数据
        this.playbackAnswer  = function(paintData){
            var self = this;
            var  Dindex = 0;
            isPlay =  true; //没播放
            var qinaaa = setInterval(function(){
                Dindex++; //每一个笔画的索引
                if(playIndex2 == self.answerPaints.length){
                    //停止播放
                    playIndex2 = 0;
                    isAnswer = true;
                    clearInterval(qinaaa); //清楚一笔播放
                    return false; //防止重复点击
                }
                if(Dindex == paintData.length){
                    clearInterval(qinaaa); //清楚一笔播放
                    // playIndex ++;
                    if(self.answerPaints.length>1){
                        if(playIndex2 == self.answerPaints.length){
                            //停止播放
                            playIndex2 = 0;
                            isAnswer = true;
                            clearInterval(qinaaa); //清楚一笔播放
                            return false; //防止重复点击
                        }else{

                            playIndex2 ++;
                            self.playbackAnswer(self.answerPaints[playIndex2]);
                        }

                    }
                    // console.log("每一个笔画索引==结束==",Dindex)
                }else{
                    // console.log("每一个笔画索引====",Dindex)
                    for (var i = 0; i <= 1; i += 0.01) {
                        // Dindex++
                        // 线性算法生成路径点
                        // console.log("每一个笔画索引===2=",Dindex,[paintData.x[Dindex - 1]])
                        var dpx1 = [paintData[Dindex - 1][0],[paintData[Dindex][0]]];

                        var dpy1 = [paintData[Dindex - 1][1],[paintData[Dindex][1]]];

                        var px = self.math.catmullRomInterpolation(dpx1, i);
                        var py = self.math.catmullRomInterpolation(dpy1, i);
                        if (!isNaN(px) && !isNaN(py) ) {
                            self.answerbmd.draw(self.bmd3, px+self.answerPos.x, py+self.answerPos.y);
                        }
                    }
                }

            },100)
        }

