(function (window, game) {
    var state_name = "01";
    var images_path = "./states/" + state_name + "/images/";
    var audio_path = "./states/" + state_name + "/images/";
    var BackgroundColor = 0xffffff; //游戏画布背景
    game.state.add(state_name, function () {
        this.init = function (data) {
            game.scale.pageAlignHorizontally = true; //水平居中
            game.scale.pageAlignVertically = true; //垂直居中
            game.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL;
            game.stage.setBackgroundColor(BackgroundColor);
            if (data === undefined) {
                //console.log("封面模拟数据");
                data = {
                    "class": "Light Mystery",
                    "label": "live Broadcasting Class Pre-school"
                }
            }
            this.data = data;
        };
        this.preload = function () {
            // game.load.spritesheet("wait1","./common_images/spritesheet111.png",500,500,8);//等待
        }
        this.create = function () {
            //添加进度动画
            // gameScoreAnimtions();
            // var correct_animation = game.add.image(game.width/2-150, 125, 'wait1'); //加载进度动画
            // correct_animation.animations.add('run');
            //  // correct_animation.animations.play('run', 1, true, 1);
            // gameManager.finalscoreCall(c); //测试排行榜数据
            // console.log('ssssssssss',gameManager.tid,gameManager.pageCount-1);
            // gameLoadingAnimation(true);//显示
            // console.log(game.width,game.height);
            // gameManager.renderScoreRank(ph);//显示排行榜测试




            if (this.data.background_image != "") {
                var bg = game.add.image(0, 0, game.stateIndex + 'cover-bg');
                bg.width = game.width;
                bg.height = game.height;
            }
            var d;

            if (this.data.overviewImage != "" && this.data.overviewImage != undefined) {

                d = game.add.image(this.data.pos.x, this.data.pos.y, game.stateIndex + 'overviewImage');
                // d.anchor.setTo(0.5,0.5);
                if (this.data.overviewAnimtion != "" && this.data.overviewAnimtion != undefined) {
                    gameDisplayAnimations(this.data.overviewAnimtion, d);
                }
                // game.add.tween(d).to({angle: 360},7000,Phaser.Easing.Bounce.Outue, true,1, false);
            }
            console.log('展示数据====', this.data)
            //展示图片组
            if (this.data.body) {
                for (var i = 0; i < this.data.body.length; i++) {
                    if (this.data.body[i].image != undefined && this.data.body[i].image != "") {
                        console.log('展示数据==动画=1=', this.data.image);
                        var current = game.add.image(this.data.body[i].pos.x, this.data.body[i].pos.y, game.stateIndex + 'displayImage' + i);
                        // console.log('展示数据==动画=2=',current);
                        if (this.data.body[i].imageAnimation != undefined && this.data.body[i].imageAnimation != "") {
                            console.log('展示数据==动画=2=', this.data.image);
                            current.alpha = 0;
                            gameAdmissionAnimations(this.data.body[i].imageAnimation, current, this.data.body[i].pos.x, this.data.body[i].pos.y);
                        }
                    }

                }
            }
            // console.log('sss',d)
            // d.alpha = 0
            if (this.data.overviewImage != "" && this.data.overviewImage != undefined || this.data.background_image != "") {
                //添加公共部分咋最外层
                gameCommon();
            }
            if (gameManager.tid != gameManager.pageCount - 1) {
                //不是 最后一页
                gameCommon();
            } else {
                gameCommon(true);
            }

            // game.sound.play("Sound_Wow_stupendous");
            var title = game.add.text(this.data.text_pos.x, this.data.text_pos.y, this.data.title, { fill: this.data.fill_color, fontSize: this.data.font_size });
        }
    });

})(window, game);