game.state.add("statemanager",function(){
    var self = this;
    this.init = function(){
        if(game.stateIndex == undefined){
            game.stateIndex = 0;
        }
        game.data = json_data.courseware;

    };
    this.preload = function(){

    };
    this.create = function(){

        if(game.willGo != undefined){//检查是否有未执行的跳转,直接跳转
            game.stateIndex = game.willGo;
            game.willGo = undefined;//清空任务
        }
        if(game.stateIndex<0){
            game.stateIndex =0
        }
        // if(game.stateIndex>0){
        //     if(!gameManager.is_teacher){
        //         gameManager.getRecord();  //拉取服务器数据
        //     }
        // }

        var currentState = game.data[game.stateIndex];
        if(currentState == undefined){
            // alert("课件数据异常");

            return;
        }



        clearInterval(game.temp_t);
        game.state.start(currentState.state,true,false,currentState.data);
        game.loaded = true;

    }
});