(function(window,game){
    var state_name = "111";
    var images_path = "./states/"+state_name+"/images/";
    var audio_path = "./states/"+state_name+"/audios/";
    var BackgroundColor = 0xffffff; //游戏画布背景
    game.state.add(state_name,function(){
        var self = this;
        var bFlga = true;
        var read_speak = false;
        var recoding = false;
        this.volumeView = null;
        var btn = null;   var btn_animation = null ; var btn1 = null;
        this.init=function(data){
            game.scale.pageAlignHorizontally = true; //水平居中
            game.scale.pageAlignVertically = true; //垂直居中
            game.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL;
            game.stage.setBackgroundColor(BackgroundColor);
            if(data === undefined){
                data = {
                    "image": "./states/world_record_6/images/pic6.png",
                    "pos": {
                        "x": 140,
                        "y": 150
                    },
                    "text": "Colors"
                }
            }
            bFlga = true;
            read_speak = false;
            recoding = false;
            self.volumeView = null;
            this.data = data;
            this.Score = 0;
            btn = null; btn_animation = null; btn1 = null;
            this.game_but = null;
        };
        this.preload = function(){

        };

        this.create = function(){
            var self= this;
            var title = game.add.text(this.data.text_pos.x,this.data.text_pos.y,this.data.title,{fill:this.data.fill_color,fontSize:this.data.font_size});
            if (this.data.image!=""&&this.data.image!=undefined){
                if(this.data.pos!=""){
                    var qun_image=game.add.image(this.data.pos.x,this.data.pos.y,game.stateIndex+'pic');
                    if (qun_image.width==1086){

                    }
                }else{
                    var qun_image=game.add.image(180,140,game.stateIndex+'pic');
                }

            }
            if(this.data.overviewImage !="" && this.data.overviewImage !=undefined){
                game.add.image(this.data.overviewImage_pos.x,this.data.overviewImage_pos.y,game.stateIndex+'overviewImage');

            }
            //所有游戏类在初始化完成后添加蒙版
            if(game.is_teacher){
                //老师显示界面
                var c = 0x6dc7eb;
                var  q_text_length = 4;
                var bg = game.add.image(60,550,'go');
                this.game_but =game.add.text(5,10,"GO",{fill:"#000000",fontSize:60,fontWeight:"normal",boundsAlignH: "center", boundsAlignV: "middle"});
                this.game_but.setTextBounds(35, 38, 16*q_text_length/2, 30);
                bg.addChild(this.game_but);
                this.game_but.inputEnabled = true;
                var self = this;
                this.game_but.events.onInputUp.add(function(){
                    if(!bFlga){
                        return false; //防止重复点击
                    }
                    if(bFlga){
                        bFlga = false;
                        var t = 9;
                        var daojis = setInterval(function(){
                            t--;
                            if(t===0){
                                self.game_but.text = "END";
                                self.game_but.fontSize=40;
                                c = 0xF5F5F5;
                                self.game_but.style.color = 0xCD0000;
                                clearInterval(daojis);
                                bFlga=true;
                                return false;
                            }
                            self.game_but.text = t+"s";
                        },1000);
                    }


                    gameManager.startTesting();
                });


            }else{
                game.show_mengban();
                //添加 倒计时进度 条
                game.progressBar = this.add_progress_bar(0, 0, 758);
                game.progressBar.alpha = 0;

            }

            //添加公共部分咋最外层
            gameCommon();
            // setTimeout(function () {
            //     self.startRecord()
            //     // setTimeout(function () {
            //     //     //停止录音测试
            //     //     self.recordcall("scores",100)
            //     // },3000)
            // },2000)
        };
        var timerBarHeight = 10; //timer bar height
        var timerBarColour = 0x43CD80; //timer bar colour 进度条颜
        /*!
         *  绘制定时器进度条 Progress bar
         */
        this.add_progress_bar= function(anime,x,y){
            var progress = game.add.graphics(0,0);
            progress.beginFill(timerBarColour);//颜色16进制，透明度0-1之间
            progress.drawRect(0, 0, game.width, timerBarHeight);//矩形
            progress.endFill();
            var sprite = game.add.sprite(x, y, progress.generateTexture());
            progress.destroy();
            return sprite;
        };
        //开始 录音
        this.startRecord = function(){
            // if(game.btn){
            //     return false;
            // }
            if(!recoding){
                read_speak = true;
                recoding = true;
                // self.addVolume().alpha = 0;
                // btn.alpha = 0;
                btn_animation = game.add.image(440,560, 'luyin'); //加载进度动画
                btn1 =game.add.image(430,550, 'luyinz');
                btn1.width = btn1.width *0.6;
                btn1.height = btn1.height *0.6;
                btn_animation.animations.add('run');
                btn_animation.animations.play('run', 7, true, 1);
                    setTimeout(function () {
                        if(btn_animation!=null){
                            btn_animation.alpha = 0;
                        }
                        if(btn1!=null){
                            btn1.alpha = 0;
                        }
                        self.Score= Math.round(100);
                        gameManager.stars(self.Score); //demo使用
                        // gameManager.addRecord(self.Score); //上传服务器 数据
                        ScoreAnimtions(self.Score);
                        recoding = false;   //结束录音奖励假的星星数
                    },6000)

            }else{

            }

        };
        this.waiting = function(hide){
            if(self.waitingView == undefined){
                self.waitingView = game.add.sprite(game.width/2-150,game.height/2-150,"wait");
                self.waitingView.animations.add("w");
            }
            if(hide){
                game.show_mengban(true); //隐藏蒙版
            }else{
                game.show_mengban();
            }
            if(hide){
                self.waitingView.alpha = 0;
            }else{
                self.waitingView.alpha = 1;
                self.waitingView.animations.play("w",16,true);
            }
        };

        // 音量传入
        this.recordcall = function(type,data){
            console.log(self.volumeView);
            if(type == "volume"){
                var index = Math.round(data/30 * 10);
                if(recoding){
                    //有音量
                    self.volumeView.frame = index-1;
                }
            }
            if(type == "scores"){
                self.waiting(true);
                self.submitScore(data,2);
            }
        };

        this.addVolume = function(){
            if(self.volumeView == null){
                btn.alpha = 0;
            }
            return self.volumeView;
        };
        //所有游戏类实现以下2个方法 提交分数
        this.submitScore = function(scores,animation){

            if(scores <=1){
                scores =Math.random()*0.5+1;
                animation = 2;
            }
            scores = Number(scores);
            if(isNaN(scores)){
                scores =Math.random()*0.5+1  ;
            }
            //不传 分手 播放 你很棒假动画

            // gameManager.sendScore(Math.round(scores*20),100,animation);
            btn_animation.alpha = 0;
            btn1.alpha =0;
            recoding = false;   //结束录音奖励假的星星数
            self.Score= Math.round(scores*20);
            gameManager.stars(self.Score); //demo使用
            gameManager.addRecord(self.Score); //上传服务器 数据
            ScoreAnimtions(self.Score);
            btn_animation.alpha =0 //加载进度动画
            btn1.alpha = 0

            game.progressBar.alpha = 1;
            // gameManager.sendScore(Math.round(Score),100,1); //传递分数 给直播云

        }
        /**==翻页笔调用页面开始答题==**/
        this.answers_startgame= function () {
            var c = 0x6dc7eb;
            if(game.is_teacher){
                if(this.game_but != null){
                    var self = this;
                    if(!bFlga){
                        return false; //防止重复点击
                    }
                    if(self.game_but.text != "END") {
                        if (bFlga) {
                            bFlga = false;
                            var t = 9;
                            var daojis = setInterval(function () {
                                t--;
                                if (t === 0) {
                                    self.game_but.text = "END";
                                    self.game_but.fontSize = 40;
                                    c = 0xF5F5F5;
                                    self.game_but.style.color = 0xCD0000;
                                    clearInterval(daojis);
                                    bFlga = true;
                                    return false;
                                }
                                self.game_but.text = t + "s";
                            }, 1000);
                        }
                    }
                    if(self.game_but.text != "END"){
                        gameManager.startTesting();
                    }

                }
            }
        }
        /***这是运行游戏定时器的功能 游戏中的定时器 定义***/

        this.startgame = function(){
            // if(time_out == undefined){
            //     time_out = 7;
            // }
            game.progressBar.alpha = 1;
            game.show_mengban(true);

            self.startRecord(); //开启录音
            recoding = true;
            var timerStart = 8000; //timer start number  10秒
            game.add.tween(game.progressBar).to({width:0,alpha:0}, timerStart, Phaser.Easing.Linear.None, true).onComplete.add(function () {


            }); //在到及时 减掉本身宽度

            //recoding = true;
        }

        this.stopgame = function (data) {
            btn.alpha = 1;
            btn_animation.alpha = 0;
            btn1.alpha =0
        }
    });

    this.shutdown = function(){
        self.submitScore(0,3); //提交
        //clearInterval(game.temp_t);
        gameManager.log("清理定时器");
    }
})(window,game);