var show_gulp = false;
var is_showTime = false;
//小组排名
function gulpView(content) {
    var gulp_view = document.getElementById("gulp_view");
    if (is_showTime) { return false };
    if (show_gulp) {
        //隐藏不在线上
        gulp_view.setAttribute("style", "display:none;");
        var gulp_container = document.getElementById("gulp_container");
        var list = gulp_container.getElementsByTagName('li');
        // console.log('长度',list)
        for (var n = 0; n < list.length; n++) {
            gulp_container.removeChild(gulp_container.getElementsByTagName('li')[n]);
            n--;
        };
        show_gulp = false;
        return false;
    }
    setTimeout(function () {
        is_showTime = false; //避免消息很慢没有渲染完
    }, 100)
    is_showTime = true;
    show_gulp = true;
    gameManager.requestStudentsList();
    // testGILP();
}
