game.show_mengban = function(remove,text){
    if(remove){
        game.mengban.x = -1024;
        game.mengban.destroy();
    }else{
        var graphics = game.add.graphics(0, 0);
        graphics.beginFill(0xFFFFF0);
        graphics.drawRect(0, 0, game.world.width,  game.world.height);   //大小130*150
        game.mengban = game.add.image(0, 0, graphics.generateTexture());
        game.mengban.alpha = 0;
        //渐变
        game.mengban.inputEnabled = true;
        graphics.destroy();
        game.mengban.x = 0;
    }

};
//启用场景
game.init_game = function(docid,tid,is_teacher){
    console.log("游戏","初始化传入docid,tid,is_teacher");
    game.docid = docid; //获取的json数据
    game.tid = tid;//页数
    game.is_teacher = is_teacher;//是否是老师
    game.state.start('boot');
};
game.gotoStage = function(index){

    if(game.loaded){
        game.stateIndex = index;
        game.state.start("statemanager",true,false);
    }else{
        //当有指令过来,游戏没有就绪的话 存入待跳转指令,只保留最后一条
        game.willGo = index;
    }

};
game.start_game = function(tid){
    console.log("学生开始答题");
    game.state.getCurrentState().startgame();
};
game.stop_game = function(data){
    console.log("结束答题===",data);
    game.state.getCurrentState().stopgame(data);
};
//翻页笔调用
game.answers_start = function () {
    game.state.getCurrentState().answers_startgame();
}