(function (window) {


  var Point = function (x, y) {
    this.x = x;
    this.y = y;
  };

  var Rectangle = function (x, y, width, height) {
    this.x = x;
    this.y = y;
    this.width = width;
    this.height = height;
  };


  // Initial Setup
  // -------------

  var numPoints = 64;
  var squareSize = 250.0;
  var origin = new Point(0, 0);
  var diagonal = Math.sqrt(squareSize * squareSize + squareSize * squareSize);
  var halfDiagonal = 0.5 * diagonal;
  var angleRange = 15.0 * Math.PI / 180.0;
  var anglePrecision = 2.0 * Math.PI / 180.0;
  var phi = 0.5 * (-1.0 + Math.sqrt(5.0));


  // Utility Functions
  // -----------------

  var resample = function (points, n) {
    var interval = pathLength(points) / (n - 1);
    var delta = 0.0;
    var newpoints = new Array(points[0]);

    for (var i = 1; i < points.length; i++) {
      var d = distance(points[i - 1], points[i]);
      if ((delta + d) >= interval) {
        var qx = points[i - 1].x + ((interval - delta) / d) * (points[i].x - points[i - 1].x);
        var qy = points[i - 1].y + ((interval - delta) / d) * (points[i].y - points[i - 1].y);
        var q = new Point(qx, qy);
        newpoints[newpoints.length] = q;
        points.splice(i, 0, q);
        delta = 0.0;
      } else {
        delta += d;
      }
    }

    if (newpoints.length == n - 1) {
      newpoints[newpoints.length] = new Point(points[points.length - 1].x, points[points.length - 1].y);
    }

    return newpoints;
  };

  var indicativeAngle = function (points) {
    var c = centroid(points);

    return Math.atan2(c.y - points[0].y, c.x - points[0].x);
  };

  var rotateBy = function (points, radians) {
    var c = centroid(points);
    var cos = Math.cos(radians);
    var sin = Math.sin(radians);
    var newpoints = new Array();

    for (var i = 0; i < points.length; i++) {
      var qx = (points[i].x - c.x) * cos - (points[i].y - c.y) * sin + c.x;
      var qy = (points[i].x - c.x) * sin + (points[i].y - c.y) * cos + c.y;
      newpoints[newpoints.length] = new Point(qx, qy);
    }

    return newpoints;
  };

  var scaleTo = function (points, size) {
    var b = boundingBox(points);
    var newpoints = new Array();

    for (var i = 0; i < points.length; i++) {
      var qx = points[i].x * (size / b.width);
      var qy = points[i].y * (size / b.height);
      newpoints[newpoints.length] = new Point(qx, qy);
    }

    return newpoints;
  };

  var translateTo = function (points, pt) {
    var c = centroid(points);
    var newpoints = new Array();

    for (var i = 0; i < points.length; i++) {
      var qx = points[i].x + pt.x - c.x;
      var qy = points[i].y + pt.y - c.y;
      newpoints[newpoints.length] = new Point(qx, qy);
    }

    return newpoints;
  };

  var vectorize = function (points) {
    var sum = 0.0;
    var vector = new Array();

    for (var i = 0; i < points.length; i++) {
      vector[vector.length] = points[i].x;
      vector[vector.length] = points[i].y;
      sum += points[i].x * points[i].x + points[i].y * points[i].y;
    }

    var magnitude = Math.sqrt(sum);

    for (var i = 0; i < vector.length; i++) {
      vector[i] /= magnitude;
    }

    return vector;
  };

  var optimalCosineDistance = function (v1, v2) {
    var a = 0.0;
    var b = 0.0;

    for (var i = 0; i < v1.length; i += 2) {
      a += v1[i] * v2[i] + v1[i + 1] * v2[i + 1];
      b += v1[i] * v2[i + 1] - v1[i + 1] * v2[i];
    }

    var angle = Math.atan(b / a);

    return Math.acos(a * Math.cos(angle) + b * Math.sin(angle));
  };

  var distanceAtBestAngle = function (points, t, a, b, threshold) {
    var x1 = phi * a + (1.0 - phi) * b;
    var f1 = distanceAtAngle(points, t, x1);
    var x2 = (1.0 - phi) * a + phi * b;
    var f2 = distanceAtAngle(points, t, x2);

    while (Math.abs(b - a) > threshold) {
      if (f1 < f2) {
        b = x2;
        x2 = x1;
        f2 = f1;
        x1 = phi * a + (1.0 - phi) * b;
        f1 = distanceAtAngle(points, t, x1);
      } else {
        a = x1;
        x1 = x2;
        f1 = f2;
        x2 = (1.0 - phi) * a + phi * b;
        f2 = distanceAtAngle(points, t, x2);
      }
    }

    return Math.min(f1, f2);
  };

  var distanceAtAngle = function (points, t, radians) {
    var newpoints = rotateBy(points, radians);

    return pathDistance(newpoints, t);
  };

  var centroid = function (points) {
    var x = 0.0, y = 0.0;

    for (var i = 0; i < points.length; i++) {
      x += points[i].x;
      y += points[i].y;
    }

    x /= points.length;
    y /= points.length;

    return new Point(x, y);
  };

  var boundingBox = function (points) {
    var minX = +Infinity, maxX = -Infinity, minY = +Infinity, maxY = -Infinity;

    for (var i = 0; i < points.length; i++) {
      minX = Math.min(minX, points[i].x);
      minY = Math.min(minY, points[i].y);
      maxX = Math.max(maxX, points[i].x);
      maxY = Math.max(maxY, points[i].y);
    }

    return new Rectangle(minX, minY, maxX - minX, maxY - minY);
  };

  var pathDistance = function (pts1, pts2) {
    var d = 0.0;

    for (var i = 0; i < pts1.length; i++) {
      d += distance(pts1[i], pts2[i]);
    }

    return d / pts1.length;
  };

  var pathLength = function (points) {
    var d = 0.0;

    for (var i = 1; i < points.length; i++) {
      d += distance(points[i - 1], points[i]);
    }

    return d;
  };

  var distance = function (p1, p2) {
    var dx = p2.x - p1.x;
    var dy = p2.y - p1.y;

    return Math.sqrt(dx * dx + dy * dy);
  };

  var deg2Rad = function (d) {
    return (d * Math.PI / 180.0);
  };


  // Template Initialization
  // -----------------------


  // 计算坐标匹配
  // -----------------------

  var getSingleScore = function (points_self, points_stand) {
    var points = [];
    var points1 = [];
    for (var i in points_self) {
      if (points_self[i].x != undefined && points_self[i].y != undefined) {
        points.push(points_self[i]);
      }
    }
    for (var n in points_stand) {
      if (points_stand[n].x != undefined && points_stand[n].y != undefined) {
        points1.push(points_stand[n]);
      }
    }

    if (points.length == 0 || points1.length == 0) {
      return 0;
    }


    points = resample(points, numPoints);
    var radians = indicativeAngle(points);
    points = rotateBy(points, -radians);
    points = scaleTo(points, squareSize);
    points = translateTo(points, origin);
    var vector = vectorize(points);

    points1 = resample(points1, numPoints);
    var radians1 = indicativeAngle(points1);
    points1 = rotateBy(points1, -radians1);
    points1 = scaleTo(points1, squareSize);
    points1 = translateTo(points1, origin);
    var vector1 = vectorize(points1);

    var score = distanceAtBestAngle(points, points1, -angleRange, +angleRange, anglePrecision);
    var r = (1.0 - score / halfDiagonal);

    if (r <= 0) {
      r = Number((Math.random() * 0.4 + 0.5).toFixed(2));
    }
    if (isNaN(r)) {
      r = Number((Math.random() * 0.4 + 0.5).toFixed(2));
    }

    return r;

  }
  var getScore = function (points_a, points_b) {
    var points = JSON.parse(JSON.stringify(points_a));
    var points1 = JSON.parse(JSON.stringify(points_b));

    var score_list = [];
    var result = {
      score_list: [],
      score: -1,
      all_score: 0,
      avg_scores: 0
    }

    if (points.length == 0 || points1.length == 0) {
      return result;
    }
    var all_score = 0;
    var p1s = [];
    var p2s = [];
    for (var i in points1) {
      var score = 0;
      var p1 = points[i];
      var p2 = points1[i];

      if (p1 == undefined) {  //没有该笔则0分
        score = 0;
      } else {
        score = getSingleScore(p1, p2);
        if (isNaN(score)) {
          score = 0;
        }
      }

      all_score += score;
      score_list.push(score);

      for (var m in p2) {
        if (p2[m].x != undefined && p2[m].y != undefined) {
          p2s.push(p2[m]);
        }

      }
      p1 = [];
      p2 = [];
    }
    for (var n in points) {
      var p1 = points[n];
      for (var m in p1) {
        if (p1[m].x != undefined && p1[m].y != undefined) {
          p1s.push(p1[m]);
        }
      }
    }
    result.score_list = score_list;
    result.score_avg = all_score / score_list.length;
    result.all_score = getSingleScore(p1s, p2s);

    //根据笔画得分 和 笔顺得分 权重计算
    result.score = ((result.all_score * 0.7 + result.score_avg * 0.3) * 100).toFixed(2);

    if (result.score < 0 || result.score > 100) {
      result.score = -1;
    }

    return result;
  }

  window.newDrawGetScore = getScore;

})(window);

