var xmlHttp;

function createxmlHttpRequest() {
    if (window.ActiveXObject) {
        xmlHttp = new ActiveXObject("Microsoft.XMLHTTP");
    } else if (window.XMLHttpRequest) {
        xmlHttp = new XMLHttpRequest();
    }
}
function doGet(url,callback){
    // 注意在传参数值的时候最好使用encodeURI处理一下，以防出现乱码
    createxmlHttpRequest();
    xmlHttp.open("GET",url);
    xmlHttp.send(null);
    xmlHttp.onreadystatechange = function() {
        // console.log('111111111xmlHttp===',xmlHttp)
        //console.log('xmlHttp===',xmlHttp.readyState == 4 , xmlHttp.status == 200)
        if ((xmlHttp.readyState == 4) && (xmlHttp.status == 200)) {
            //alert('success');
            // callback()
            console.log('请求成功',xmlHttp.responseText);
            callback(JSON.parse(xmlHttp.responseText));
        } else {
             // alert('fail');
        }
    }
}
function doPost(url,data,callback) {
    var xhr = new XMLHttpRequest();
    xhr.open("POST", url, true);
    xhr.timeout ='30000';
    xhr.setRequestHeader("Content-type","application/x-www-form-urlencoded");
    xhr.onreadystatechange = function(){

        var XMLHttpReq = xhr;
        console.log(XMLHttpReq.readyState);
        if (XMLHttpReq.readyState == 4) {
            console.log(XMLHttpReq.status);
            if (XMLHttpReq.status == 200) {
                var text = XMLHttpReq.responseText;
                callback(JSON.parse(text));
            }else{
                console.log(XMLHttpReq.status)
                callback(false);
                if(gameManager){
                    var s = {"status":XMLHttpReq.status,"timeout":XMLHttpReq.timeout,"sid":gameManager.sid,"tid":gameManager.tid,"lessonID":gameManager.lesson_id}
                }else {
                    var s = {"status":XMLHttpReq.status,"timeout":XMLHttpReq.timeout}
                }
                var test = JSON.stringify(s)
                aliyun_log("err_request",test)
            }
        }
    };
    // xhr.ontimeout = function(event){
    //     aliyun_log('请求超时！');
    //     xhr.send(dataser(data));
    // }

    xhr.send(dataser(data));
}
//POST 请求json 转成服务器请求字符串
function dataser(obj) {
    var str = "";
    for(var prop in obj){
        str += prop + "=" + obj[prop] + "&"
    }
    return str;
};
