var headers=document.getElementById('headers');
var oFirst = document.getElementById('first');
var oSecond = document.getElementById('second');
var aA=document.getElementById('a1');
var aB=document.getElementById('a2');
aA.onclick = function(){
    oFirst.style.display = 'block';
    oSecond.style.display = 'none';
    headers.className='head'
};
aB.onclick = function(){
    oFirst.style.display = 'none';
    oSecond.style.display = 'block';
    headers.className="head_bg";
};
