
(function (window, game) {
    game.state.add('201', {
        init: function () {
            game.scale.pageAlignHorizontally = true; //水平居中
            game.scale.pageAlignVertically = true; //垂直居中
            game.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL;
            var data = game.data;
            var index = game.stateIndex;
            this.data = data[index];




        },
        create: function () {
           
            if (game.is_teacher) {
                gameManager.startTesting();
                drawTask(this.data, JSON.parse(JSON.stringify(this.data.data.Pdata)), game);
            }
            //添加公共部分咋最外层
            gameCommon();

            var other_images = this.data.data.other_images;

            if (other_images) {
                for (var tem_i = 0; tem_i < other_images.length; tem_i++) {
                    other_images[tem_i]['image'] !== '' ? game.add.image(other_images[tem_i]['pos']['x'], other_images[tem_i]['pos']['y'], game.stateIndex + 'other_images' + tem_i + 'temp') : null;
                }
            }


            //用户写的字
            this.arr = [];

            function removeEmptyArr(arr) {
                for (var i = 0; i < arr.length; i++) {
                    if (arr[i].length === 0) {
                        arr.splice(i, 1);
                        i = i - 1;
                    }
                }
                return arr;
            }

            function mypen(color) {
                var p = game.add.graphics(0, 0);
                p.lineStyle(20, color, 1);

                return p;
            }


           
            function drawTask(data, arr, game) {

                if (arr && arr.length > 0) {
                    var item = arr.shift();
                    if (item) {
                        for (var k = 0; k < item.length; k++) {
                            item[k]['index'] = k;
                        }
                        drawLine(item, 0.001, mypen(0xff00ff), drawTask, arr, game, data);
                    }
                }
            }

            function drawLine(arr, time, graphics, callback, cbargs, game, data) {

                var timer = setTimeout(function () {


                    item = arr.shift();
                    if (item) {
                        if (item['index'] === 0) {
                            graphics.moveTo(transformXY(item.x, 100, 2.5), transformXY(item.y, 0, 2.5));
                        } else {
                            graphics.lineTo(transformXY(item.x, 100, 2.5), transformXY(item.y, 0, 2.5));
                        }
                    }

                    if (arr.length > 0) {
                        drawLine(arr, time, graphics, callback, cbargs, game, data);
                    } else {
                        callback && callback(data, cbargs, game);
                    }
                }, time * 1000);
                if (game.stateIndex !== data.page) {
                    clearTimeout(timer);
                }

            }

            function transformXY(x, px, scale) {
                return (x + px) * scale;
            }


            paintWord(game, this.arr);

            function paintWord(game, arr) {
                this.flag = true;
                this.arr = arr
                var that = this;

                game.input.touch.touchStartCallback = function (e) {
                    if (game['data'][game['stateIndex']]['type'] !== "newdraw") {
                        cancleTouchEvent(game);
                    } else {
                        that.graphics = mypen(0xFF0000);

                        that.flag = false;
                        that.arr.push([]);
                    }


                };
                game.input.touch.touchMoveCallback = function (e) {
                    if (game['data'][game['stateIndex']]['type'] !== "newdraw") {
                        cancleTouchEvent(game);
                    } else {

                        var temArr = that.arr[that.arr.length - 1];
                        if (!that.flag) {
                            that.flag = true;

                            that.graphics.moveTo(game.input.activePointer.x, game.input.activePointer.y)
                        } else {
                            that.graphics.lineTo(game.input.activePointer.x, game.input.activePointer.y)
                        }
                        temArr.push({
                            x: game.input.activePointer.x,
                            y: game.input.activePointer.y
                        });
                    }
                }
                game.input.touch.touchEndCallback = function (e) {
                    if (game['data'][game['stateIndex']]['type'] !== "newdraw") {
                        cancleTouchEvent(game);
                    } else {

                        that.graphics = null;
                    }

                }
            }

            function cancleTouchEvent(game) {
                game.input.touch.touchStartCallback = null;
                game.input.touch.touchMoveCallback = null;
                game.input.touch.touchEndCallback = null;
            }

        },
        shutdown: function (game) {

            var result = newDrawGetScore(this.arr, this.data.data.Pdata);
            var scores = result['score'];
            console.log(scores);
            console.log(this.data.data.Pdata);
            console.log(this.arr)
            scores = parseInt(scores, 10);
            scores = isNaN(scores) ? 0 : scores;
            console.log(scores);
            gameManager.stars(Math.round(scores));
            ScoreAnimtions(scores);
        }
    }


    );


})(window, game);
